const state={   //state
    showFooter: true,
    changableNum:0
    //
  };
const getters = {   //state()
   isShow(state) {  //showFooter
      return state.showFooter
   }
};
const mutations = {
   show(state) {   //statestate();
       state.showFooter = true;
   },
   hide(state) {  //
       state.showFooter = false;
   },
   newNum(state,sum){ //statesum
      state.changableNum+=sum;
   }
};
const actions = {
   hideFooter({commit}) {  //mutationsstore 
       commit('hide');
   },
   showFooter({commit}) {  //
       commit('show');
   },
   getNewNum({commit},num){   //num
       commit('newNum',num)
    }
};
export default {
   namespaced: true, //
   state,
   getters,
   mutations,
   actions
}


