import { getToken, setToken, removeToken } from '@/utils/auth'
import { Notification }from 'element-ui'
import { logIn,logOut} from '@/api/login'
import router from '@/router'
const state={   //state
    token: getToken(),
    userInfo:{},
    language:""
    //
  };
const getters = {   //state()
   newToken(state) {  //showFooter
      return state.token
   },
   useDepartId(state){
      return state.userInfo.departmentId
   },
   role(state){
      return state.userInfo.role
   }
};
const mutations = {
   getToken(state,token) {   //statestate();
      state.token = token;
      setToken(token)
      router.push({name:"monitor"})
   },
   setUserInfo(state,userInfo) {
      state.userInfo = userInfo;
      console.log(state.userInfo)
   },
   removeToken(state) {  //
     console.log(state)
       state.token = ""
       removeToken()
       router.push({name:"login"})
   },
   setLanguage(state,language){
     state.language = language
   },
};
const actions = {
  setLanguage({commit},data){
    commit('setLanguage',data)
  },
   logeIn({commit,state},data) {  //mutationsstore 
      logIn(data).then((res)=>{
        console.log(res)
        var data = res
        console.log(res)
        if(data.code==200 && data.success){
          console.log(data.data.token)
          commit('getToken',data.data.token)
          commit('setUserInfo',data.data)
          console.log(state.language)
          if(state.language=='en'){
             Notification({
               title: 'Success',
               message: 'Login Successful',
               type: 'success',
               duration:1000
             });
          }else{
            Notification({
               title: '',
               message: '',
               type: 'success',
               duration:1000
             });
          }
        }else{
            // if(state.language=='en'){
            //    Notification.error({
            //       title: 'Note',
            //       message: data.message,
            //       duration:1000
            //   });
            // }else{
            //    Notification.error({
            //       title: '',
            //       message: data.message,
            //       duration:1000
            //   });
            // }
        }
      })
   },
   logeOut({commit,state},data) {  //
     logOut(data).then((res)=>{
       var data = res;
       console.log(res.code)
       if(data.code=='200' && data.success){
         commit('removeToken');
         if(state.language=='en'){
            Notification({
               title: 'Success',
               message: "Exit successfully",
               type: 'success',
               duration:1000
             });
         }else{
            Notification({
               title: '',
               message: "",
               type: 'success',
               duration:1000
             });
         }
       }else{
         if(state.language=='en'){
            Notification.error({
               title: 'Note',
               message: data.message,
               duration:1000
             });
         }else{
            Notification.error({
               title: '',
               message: data.message,
               duration:1000
             });
         }
       }
     })


   }
};
export default {
   namespaced: true, //
   state,
   getters,
   mutations,
   actions
}



