import Vue from 'vue';
import VueI18n from 'vue-i18n';
Vue.use(VueI18n);
import store from '../store/'//store
import ElementLocale from 'element-ui/lib/locale';
import enLocale from 'element-ui/lib/locale/lang/en'        //Element UI
import zhLocale from 'element-ui/lib/locale/lang/zh-CN'
// 
import zh from './zh';
import en from './en';
// vue-i18ni18n
const i18n = new VueI18n({
    // 
    locale: store.state.login.language  || 'zh', // 
    messages: {
      zh: Object.assign(require('@/utils/zh'), zhLocale),
      en: Object.assign(require('@/utils/en'), enLocale),
    },
})
ElementLocale.i18n((key, value) => i18n.t(key, value))
// i18n
export default i18n;



